.Animate-heading {
  animation: fadeInAnimation 1s ease-in-out;
}

.Animate {
  animation: fadeInAnimation 1s ease-in-out;
}

@keyframes fadeInAnimation {
  0% {
    /* transform: translateY(-1%); */
    opacity: 0;
  }
  50% {
    transform: translateY(-7.5px);
    opacity: .3;
  }
}

.Animate-name {
  animation: fadeInAnimation-name-homepage 1s ease;
}

.Animate-down-arrow {
  max-width: 100%;
  animation-name: fadeInAnimation-downArrow;
  animation-iteration-count: infinite;
  animation-duration: 1s;
  -webkit-transition: all 0.9s ease;
     -moz-transition: all 0.9s ease;
       -o-transition: all 0.9s ease;
          transition: all 1s ease;
  animation-direction: alternate;

  /* animation: fadeInAnimation-downArrow 5s ease-in-out;
  animation-iteration-count: infinite; */
}

.Animate-Menu-Name {
  /* animation-delay: 600ms; */
  animation:  fadeInAnimation-menu 1s forwards;
  opacity: .7;
}
.Animate-Menu-Name:nth-child(1) {animation-delay: 100ms;}
.Animate-Menu-Name:nth-child(2) {animation-delay: 100ms;}
.Animate-Menu-Name:nth-child(3) {animation-delay: 200ms;}
.Animate-Menu-Name:nth-child(4) {animation-delay: 300ms;}
.Animate-Menu-Name:nth-child(5) {animation-delay: 400ms;}

@keyframes fadeInAnimation-menu {
  0% {
    /* transform: translateY(-1%); */
    opacity: 0;
  }
  50% {
    transform: translateY(-7.5px);
    opacity: .6;
  }
}


@keyframes fadeInAnimation-downArrow {

  0%{
    opacity: 0;
  }
  100%{
    opacity: 0.5  ;
    filter: alpha(opacity=50);
    transform: translateY(-17px);
}
}


.Animate-navbar {
  animation: fadeInAnimation-navbar .5s ease-in;
}

@keyframes fadeInAnimation-navbar {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}


.card-body {
  display: flex;
  justify-content: center;
  align-items: center;
  width:100%;
  height:110%;
  /* top: 0; */
  top: -100%;
  border-radius: 20px;
  position: absolute;
  background-color: #eeeeee25;
  backdrop-filter: blur(20px);
  transition: 1s;
}

.card-background:hover .card-body{
  top: 0
}

.card-background {
  width:100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
  position: relative;
}



