.profileBox{
  /* background: #99999B; */

  animation: animateForBox 5s ease-in-out infinite;
}

@keyframes animateForBox{
  0%{
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40% ;
  }

  50%{
    border-radius: 30% 60% 70% 30%/50% 60% 30% 60% ;
  }

  100%{
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40% ;
  }
}

:root{

  --experienceMobileCards:5;
  --cardHeight: 55svh;
  --cardTopPadding: 3em;
  --cardMargin: 5svw;


}

#experienceMobileCards{
  list-style: none;
  display: grid;
  padding-left: 0;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(var(--experienceMobileCards), var(--cardHeight));
  gap: var(--cardMargin);
  padding-bottom: calc(var(--experienceMobileCards) * var(--cardTopPadding));
  margin-bottom: var(--cardMargin);

}

#card1{
  --index: 1;
}

#card2{
  --index: 2;
}

#card3{
  --index: 3;
}

#card4{
  --index: 4;
}
#card5{
  --index: 5;
}

.experienceMobileCard{
  position: sticky;
  top: 10px;
  padding-top: calc(var(--index) * var(--cardTopPadding)*1);
}

#card1 .mobile-class-body{
  background-color: #eeeeee;
}
#card2 .mobile-class-body{
  background-color: #eeeeee;
}
#card3 .mobile-class-body{
  background-color: #eeeeee;
}
#card4 .mobile-class-body{
  background-color: #eeeeee;
}
#card5 .mobile-class-body{
  background-color: #eeeeee;
}

.mobile-class-body{
  box-sizing: border-box;
  padding: 10px;
  /* padding-left: 30px; */
  border-radius: 50px;
  box-shadow: 0 0 30px 0 rgba(0,0,0,0.5);
  height: var(--cardHeight);
  display: flex;
  flex-direction: column;
  align-items: center;
  /* border-style: solid; */
  border-color: "#d1d1d1";

}


.box1{
  aspect-ratio: 16/9;
  /* transform: scale(0.7); */
  transition: all 1.5s cubic-bezier(0.77, 0, 0.175, 1);
  filter: grayscale(100%) sepia(20%) brightness(80%);
}

.box2{
  aspect-ratio: 16/9;
  transform: scale(0.7);
  transition: all 1.5s cubic-bezier(0.77, 0, 0.175, 1);
  filter: grayscale(100%) sepia(20%) brightness(80%);
}

.box3{
  aspect-ratio: 16/9;
  transform: scale(0.7);
  transition: all 1.5s cubic-bezier(0.77, 0, 0.175, 1);
  filter: grayscale(100%) sepia(20%) brightness(80%);
}